<template>
  <div
    class="poster-page-bg"
    :style="setPosterStyle"
  ></div>
</template>

<script>
export default {
  name: "PosterBg",
  props: {
    poster: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      dafaultPosterBg:
        "background-image: linear-gradient(35.5deg,rgba(149, 222, 250, 1) 8.4%,rgba(0, 109, 208, 1) 105.1%);",
    };
  },
  computed: {
    getPosterBg() {
      return this.poster ? `url(${this.poster})` : this.dafaultPosterBg;
    },
    setPosterStyle() {
      return {
        "background-image": this.getPosterBg,
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.poster-page-bg {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-position: center top;

  &::before {
    content: "";
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    opacity: 0.7;
    background-image: linear-gradient(
      35.5deg,
      rgb(93, 0, 146) 8.4%,
      rgb(49, 0, 88) 105.1%
    );
  }
}
</style>
