<template>
  <div></div>
</template>

<script>
import { ElNotification } from "element-plus";
import { mapGetters } from "vuex";

export default {
  name: "VNotify",
  computed: {
    ...mapGetters(["messagePool"]),
  },
  watch: {
    messagePool: "showNotify",
  },
  methods: {
    showNotify({ title, message, type }) {
      ElNotification({
        title: title,
        message: message,
        type: type,
      });
    },
  },
};
</script>

