<template>
  <h3>{{ setListtitle }}</h3>
  <div class="movies-list">
    <template v-if="isExist">
      <MovieItem
        class="movies-list__item"
        v-for="(movie, key) in list"
        :key="key"
        :movie="movie"
        @mouseover="onMouseOver(movie.Poster)"
        @removeItem="onRemoveItem"
        @showModal="showMovieInfo"
      />
    </template>
    <template v-else>
      <el-empty description="no movies yet" />
    </template>
    <el-dialog
      title="Confirm the action"
      width="30%"
      v-model="dialogVisible"
    >
      <span>Do you really want to remove this from the list?</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button
            type="primary"
            @click="isComfirmed = true"
            >Confirm</el-button
          >
        </span>
      </template>
    </el-dialog>
    <!-- info modal -->
    <el-dialog
      v-model="infoModal"
      width="75%"
      :show-close="false"
      center
    >
      <MovieInfoModal
        :movie="selectedMovie"
        @closeModal="closeModal"
      />
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import MovieItem from "@/components/MovieItem";
import MovieInfoModal from "@/components/MovieInfoModal";

export default {
  name: "MoviesList",
  emits: ["changePoster"],
  components: { MovieItem, MovieInfoModal },
  props: {
    list: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      dialogVisible: false,
      infoModal: false,
      selectedMovieID: "",
    };
  },
  computed: {
    ...mapGetters("Movies", ["isSearch"]),
    isExist() {
      return Boolean(Object.keys(this.list).length);
    },
    setListtitle() {
      return this.isSearch ? "Search results" : "IMDB Top 250";
    },
    selectedMovie() {
      return this.selectedMovieID ? this.list[this.selectedMovieID] : null;
    },
  },
  methods: {
    ...mapActions("Movies", ["removeMovie"]),
    ...mapActions(["showNotify"]),
    onMouseOver(poster) {
      this.$emit("changePoster", poster);
    },
    async onRemoveItem({ id, title }) {
      await this.handleClose(id, title);
    },
    handleClose(id, title) {
      this.$confirm(`Are you sure to delete: ${title}?`)
        .then(() => {
          this.dialogVisible = false;
          this.removeMovie(id);
          this.showNotify({
            message: `${title} was removed`,
            title: "Success",
            type: "success",
          });
        })
        .catch(() => {});
    },
    showMovieInfo(id) {
      this.selectedMovieID = id;
      this.infoModal = true;
    },
    closeModal() {
      this.infoModal = false;
    },
  },
};
</script>

<style scoped lang="scss">
h3 {
  text-align: center;
  font-size: 2em;
  margin-bottom: 1em;
  color: #fff;
  text-transform: uppercase;
}
.movies-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2em;
}
</style>
