<template>
  <header class="header">
    <div class="header__container">
      <a
        class="header__logo"
        href=""
        >MovieDB</a
      >
      <el-input
        v-model.lazy="searchValue"
        class="w-50 m-2 input-search"
        placeholder="Type something"
        v-debounce="400"
      />
    </div>
  </header>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "VHeader",
  data() {
    return {
      searchValue: "",
    };
  },
  methods: {
    ...mapActions("Movies", [
      "searchMovies",
      "fetchMovies",
      "toogleSearchState",
    ]),
    searchValueChanged(val) {
      if (val) {
        this.searchMovies(val);
        this.toogleSearchState(true);
      } else {
        this.fetchMovies();
        this.toogleSearchState(false);
      }
    },
  },
  watch: {
    searchValue: "searchValueChanged",
  },
};
</script>

<style lang="scss" scoped>
.header {
  background-color: #313131;
}
.header__container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 15px 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  color: #fff;
  font-weight: 700;
  text-decoration: none;
}
.input-search {
  width: 100%;
  max-width: 250px;
}
</style>
