<template>
  <VHeader></VHeader>
  <div class="container">
    <MyLoader></MyLoader>
    <VNotify />
    <PosterBg :poster="posterBg" />
    <MoviesList
      :list="getMoviesList"
      @changePoster="changePoster"
    ></MoviesList>
    <MoviePagination
      :currentPage="getCurrentPage"
      :perPage="getMoviesPerPage"
      :total="getTotalMovies"
      @onPageChanged="onPageChanged"
    />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import MoviePagination from "@/components/Pagination";
import MoviesList from "@/components/MoviesList.vue";
import PosterBg from "@/components/PosterBg";
import MyLoader from "@/components/Loader";
import VHeader from "@/components/VHeader";
import VNotify from "@/components/VNotify";

export default {
  name: "App",
  data() {
    return {
      posterBg: "",
    };
  },
  components: {
    MoviesList,
    PosterBg,
    MoviePagination,
    MyLoader,
    VHeader,
    VNotify,
  },
  computed: {
    ...mapGetters("Movies", [
      "getMoviesList",
      "getCurrentPage",
      "getMoviesPerPage",
      "getTotalMovies",
    ]),
  },
  watch: {
    "$route.query": {
      handler: "onPageQueryChange",
      immediate: true,
      deep: true,
    },
  },
  methods: {
    ...mapActions("Movies", ["changeCurPage"]),
    onPageQueryChange({ page = 1 }) {
      this.changeCurPage(Number(page));
    },
    changePoster(poster) {
      this.posterBg = poster;
    },
    onPageChanged(page) {
      console.log("app" + page);
      this.$router.push({ query: { page } });
    },
  },
};
</script>

