<template>
  <div class="movie-item">
    <div
      class="movie-item__poster-wrap"
      :style="posterBg"
    ></div>

    <div class="movie-item__text-content">
      <div class="movie-item__actions">
        <el-button
          type="primary"
          @click="showInfoModal"
        >
          Info</el-button
        >
        <el-button
          type="danger"
          circle
          @click="removeItem"
        >
          <el-icon :size="16"> <Delete /></el-icon
        ></el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MovieItem",
  props: {
    movie: {
      type: Object,
      required: true,
    },
  },
  computed: {
    posterBg() {
      return {
        "background-image": `url(${this.movie.Poster})`,
      };
    },
  },
  methods: {
    removeItem() {
      this.$emit("removeItem", {
        id: this.movie.imdbID,
        title: this.movie.Title,
      });
    },
    showInfoModal() {
      this.$emit("showModal", this.movie.imdbID);
    },
  },
};
</script>

