  cursor: pointer;
  overflow: hidden;
  border-radius: 10px;
  transition: all 0.3s ease;

  &:hover {
    box-shadow: 0 5px 30px #c3c3c3;
    transform: translateY(-5px);

    .movie-item__poster-wrap {
      transform: scale(1.1);
    }
  }
}

.movie-item__poster-wrap {
  transition: transform 0.3s ease 0s;
