import axios from "@/plugins/axios";
import IDs from "@/store/mock/imdb_top250";
import mutations from "@/store/mutations";

const { MOVIES, CURRENT_PAGE, REMOVE_MOVIE, TOGGLE_SEARCH } = mutations;

function serializeRespons(movies) {
  return movies.reduce((acc, movie) => {
    acc[movie.imdbID] = movie;
    return acc;
  }, {});
}

const moviesStore = {
  namespaced: true,
  state: {
    top2550IDs: IDs,
    moviesPerPage: 12,
    currentPage: 1,
    movies: {},
    isSearch: false,
  },
  getters: {
    getMoviesList: ({ movies }) => movies,
    getSlicedIDs:
      ({ top2550IDs }) =>
      (from, to) =>
        top2550IDs.slice(from, to),
    getCurrentPage: ({ currentPage }) => currentPage,
    getMoviesPerPage: ({ moviesPerPage }) => moviesPerPage,
    getTotalMovies: ({ top2550IDs }) => Object.keys(top2550IDs).length,
    isSearch: ({ isSearch }) => isSearch,
  },
  mutations: {
    [MOVIES](state, data) {
      state.movies = data;
    },
    [CURRENT_PAGE](state, page) {
      state.currentPage = page;
    },
    [REMOVE_MOVIE](state, index) {
      state.top2550IDs.splice(index, 1);
    },
    [TOGGLE_SEARCH](state, bool) {
      state.isSearch = bool;
    },
  },
  actions: {
    // initMoviesStore: {
    //   handler({ dispatch }) {
    //     dispatch("fetchMovies");
    //   },
    //   // это значит что данный метод будет вынесен из модуля и будет доступен в любой части приложения
    //   root: true,
    // },
    async fetchMovies({ getters, commit, dispatch }) {
      try {
        dispatch("toggleLoader", true, { root: true });
        const { getCurrentPage, getMoviesPerPage, getSlicedIDs } = getters;
        const from = getCurrentPage * getMoviesPerPage - getMoviesPerPage;
        const to = getCurrentPage * getMoviesPerPage;
        const moviesToFetch = getSlicedIDs(from, to);
        const requestArray = moviesToFetch.map((id) => axios.get(`/?i=${id}`));
        const response = await Promise.all(requestArray);
        const movies = serializeRespons(response);
        // название мутации и данные
        // чтобы вызвать изменения в сторе через мутации
        commit(MOVIES, movies);
      } catch (error) {
        console.log(error);
      } finally {
        dispatch("toggleLoader", false, { root: true });
      }
    },
    changeCurPage({ commit, dispatch }, page) {
      console.log("store" + page);

      commit(CURRENT_PAGE, page);
      dispatch("fetchMovies");
    },
    removeMovie({ commit, dispatch, state }, id) {
      const index = state.top2550IDs.findIndex((item) => item === id);
      if (index !== -1) {
        commit(REMOVE_MOVIE, index);
        dispatch("fetchMovies");
      }
    },
    async searchMovies({ commit, dispatch }, query) {
      try {
        dispatch("toggleLoader", true, { root: true });
        const response = await axios.get(`/?s=${query}`);

        if (response.Error) {
          throw Error(response.Error);
        }
        const movies = serializeRespons(response.Search);
        commit(MOVIES, movies);
      } catch (error) {
        console.log(error.message);
        dispatch(
          "showNotify",
          {
            message: `${error.message}`,
            title: "Error",
            type: "error",
          },
          { root: true }
        );
      } finally {
        dispatch("toggleLoader", false, { root: true });
      }
    },
    toogleSearchState({ commit }, bool) {
      commit(TOGGLE_SEARCH, bool);
    },
  },
};
export default moviesStore;
